import { initCarousel, setSliderBasedOnViewWidth } from "../objects/carousel";
import { getElem, getElems } from "../tools/utilities/helper-functions";
import { CartAdd, cartAdd, t, getCart } from '@process-creative/slate-theme-tools';


const mainContainers = getElems('[data-home-product-slider]')!;

mainContainers.forEach((mainContainer) => {
  mainContainer && (() => {
    const slidesContainer = getElem('[data-slides-container]', mainContainer)!;
    const slideElems = getElems('[data-slider-slide]', slidesContainer)

    setSliderBasedOnViewWidth(setupCarousel, slidesContainer, slideElems)

    function setupCarousel(initialSlidesPerView: number){
      return initCarousel({
        container: slidesContainer,
        options: {
          centered: true,
          slidesPerView: initialSlidesPerView,
          loop: true,
          spacing: 15,
          initial: 1
        },
        dotsContainer: getElem('[data-carousel-dots]', mainContainer)
      })!.slider;
    }

  })();
});

const SELECTOR_COLLECTION_CONTAINER = '[data-home-product-slider-item]'
const SELECTOR_ADD_BUTTON = '[data-add-to-cart-button]';
const SELECTOR_VARIANT_ID = 'data-add-to-cart-variant';

const TIME_ADDED = 2000;


const btnDisable = (btn:HTMLButtonElement) => {
  btn.disabled = true;
  btn.innerHTML = `<span>${t('products.product.adding')}</span>`;
}

const btnEnable = (btn:HTMLButtonElement) => {
  btn.disabled = false;
  btn.innerHTML = `<span>${t('products.product.add')}</span>`;
}

type CartItem = {
  product_type: string
}

type Cart = {
  items_count: number,
  items: CartItem[]
}



export const onAddThemToCart = async (e:Event,container:HTMLElement, promo?:boolean)  => {

  let items:Array<CartAdd> = [];
  let state:'adding'|'added'|'pending'|'error' = 'pending';
  e.preventDefault();

  let btnAdd = container.querySelector<HTMLButtonElement>(SELECTOR_ADD_BUTTON);
  if(!btnAdd) return

  const vId = btnAdd.getAttribute(SELECTOR_VARIANT_ID);

  //This logic is for the promo sample, if it's already in cart I don't add it again
  let promoInCart = false;

  if(promo){
    await getCart().then((data:any) => {

      if(data.items_count <= 0) return;
      for (const item of data.items) {
        if(item.product_type === 'Promotions') return promoInCart = true;
      }

    });
  }
  if(promoInCart) return;

  if(vId){
    items.push({
      id: parseInt(vId),
      quantity: 1
    });
  }

  if(state !== 'pending' ) return;

  state = 'adding';
  btnDisable(btnAdd);
  if(!items) return

  try {

    // Now we'll add our item to cart
    await cartAdd({items: items});
    state = 'added';

    btnAdd.innerHTML = `<span>${t('products.product.added')}</span>`;

  } catch(e) {

    console.error(e);
    state = 'error';

    btnAdd.innerHTML = `${t('products.product.error')}`;
  }

  await new Promise(resolve => setTimeout(resolve, TIME_ADDED));
  state = 'pending';

  btnEnable(btnAdd);
}

const collectionContainers = document.querySelectorAll<HTMLElement>(SELECTOR_COLLECTION_CONTAINER);
const addBtns = document.querySelectorAll<HTMLElement>(SELECTOR_ADD_BUTTON);

document.querySelectorAll<HTMLElement>(SELECTOR_COLLECTION_CONTAINER).forEach(container => {

  if(!container) return;

  let addBtn = container.querySelector(SELECTOR_ADD_BUTTON);
  if(!addBtn) return;

  addBtn.addEventListener('click', (e) => onAddThemToCart(e,container));
});
